import {
  Box,
  HStack,
  IconButton,
  Spacer,
  useColorMode,
  useColorModeValue,
} from '@chakra-ui/react'
import React,{useState,useEffect} from 'react'
import { FaMoon, FaSun } from 'react-icons/fa'
import { useAuth } from '../context/AuthContext'
import Navlink from './Navlink'

export function Navbar() {
  const { toggleColorMode } = useColorMode()

  const {currentUser,logout} = useAuth()

  const [serverUrl, setServerUrl] = useState('/');
  
  useEffect(() => {
    if(window.location.href.indexOf('themiraj.github.io') > -1){
      setServerUrl('/google-auth/')
    }else{
      setServerUrl('/')
    }
    return () => {
      
    }
  }, [serverUrl])

  return (
    <Box
      borderBottom='2px'
      borderBottomColor={useColorModeValue('gray.100', 'gray.700')}
      mb={4}
    >
      <HStack py={4} justifyContent='flex-end' maxW='container.lg' mx='auto'>
        <Navlink to='/' name='Firbase Authentication' size='lg' />
        <Spacer />
       {!currentUser && <Navlink to={`${serverUrl}login`} name='Login' />}
        {!currentUser && <Navlink to={`${serverUrl}register`} name='Register' />}
        {currentUser && <Navlink to={`${serverUrl}profile`} name='Profile' />}
        {currentUser && <Navlink to={`${serverUrl}protected-page`} name='Protected' />}
        {currentUser && <Navlink
          to='/logout'
          name='Logout'
          onClick={async e => {
            e.preventDefault()
            // handle logout
            logout();
            
          }}
        />}
        <IconButton
          variant='outline'
          icon={useColorModeValue(<FaSun />, <FaMoon />)}
          onClick={toggleColorMode}
          aria-label='toggle-dark-mode'
        />
      </HStack>
    </Box>
  )
}
